*,
*:before,
*:after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
  margin: 0;
  background: #f5f5f5;
  font-family: 'Arial', 'Arial';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
 
.header {
    text-align: center;
    font-weight: 400;
    color: rgb(255, 255, 255);
    background: #0F1626;
    font-size: 27px;
    padding: 20px 0;
}

.inputHolder {
    margin: 15px 0;
}

.mainWrapper {
    padding: 0 10px;
}

.mainContent {
    display: flex;
    // align-items: center;
    margin-bottom: 10px;

    @media (max-width: 800px) {
        flex-direction: column;
    }
}

.inputHolder { 
    padding: 0 10px;
    display: flex; 

    input[type='text'] {
        max-width: 200px;
        width: 100%;
        padding: 5px 10px;
        font-size: 16px;
        border-radius: 8px;
        display: inline-block;
        border: 1px solid rgb(0, 0, 0);
        outline: none;
    }

    .uploadInput { 
        width: 0px;
        height: 0px;
        visibility: hidden;
        opacity: 0;
    }

    .or {
        align-items: center;
        display: inline-flex;
        padding: 0 20px;
    }

    .uploadImage {
        padding: 8px 16px;
        font-size: 16px;
        // background: #333;
        background: #0F1626;
        color: #fff;
        display: inline-block;
        border: 0;
        border-radius: 8px;
        cursor: pointer;
        outline: none;
    }
}

.imageHolder {
    width: 60%;
    display: block;
    flex: 0 0 60%;
    max-width: 900px;
    height: 70vmin;
    

    @media (max-width: 800px) {
        width: 100%;
        height: auto;
        margin-bottom: 15px;
        
    }

    &:empty {
        display: none;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 8px;
    }
}

.resultsHolder {
    padding: 30px;

    @media (max-width: 800px) {
        width: 100%;
        padding: 0;
    }

    .result {
        border: 1px solid #333;
        margin: 0 0 10px;
        padding: 10px;
        border-radius: 8px;
        background-color: white;
        

        &:first-of-type {
            border-color: #333;
            background-color: #FF5330;

            .name { color: #fff; }
            .confidence { color: #dfdfdf; }
        }

        .name {
            font-size: 20px;
            font-weight: 600;
            text-transform: uppercase;
        }

        .confidence {
            display: block;
            font-weight: 300;

            .bestGuess {
                background: rgb(255, 254, 254);
                padding: 2px 6px;
                color: #333;
                margin-left: 3px;
            }
        }
    }
}

.button {
    padding: 12px 17px;
    margin-bottom: 17px;
    font-size: 18px;
    background: #FF5330;
    color: #fff;
    display: inline;
    border: 0;
    border-radius: 8px;
    cursor: pointer;
    outline: none;
}

